import { Component, OnInit, ViewChild } from '@angular/core';
import { ToastrService } from 'ngx-toastr';
import { NgxSpinnerService } from 'ngx-spinner';
import { GlobalService } from '../../../../shared-ui/global.service';
import { UserSearch, CompanySearch } from '../../../../shared-ui/models/global.model';
import { ModalDirective } from 'ngx-bootstrap';
import { currentUser } from '../../../../layouts/home-layout/user.model';
import { JwtService } from '../../../../shared-ui/jwt.service';
import { Subscription } from 'rxjs';
import { ActivatedRoute } from '@angular/router';
import { saveAs } from 'file-saver';
import { environment } from '../../../../../environments/environment';

@Component({
  selector: 'app-userlist',
  templateUrl: './userlist.component.html',
  styleUrls: ['./userlist.component.scss']
})
export class UserlistComponent implements OnInit {
  permission = {
    view: false,
    edit: false,
    delete: false
  }
  @ViewChild("deleteUserModal", { static: false })
  public deleteUserModal: ModalDirective;
  currentUser: currentUser = new currentUser;
  pager: any = {};
  users: any;
  pagination: any;
  multiSelect: any;
  selectedUsers: any = [];
  selectedUser: any;
  order: string = 'lastName';
  reverse: boolean = true;
  companies: any;
  groups: any;
  userSearch: UserSearch = new UserSearch;
  companySearch: CompanySearch = new CompanySearch;
  userDeleteOption: any;
  companyId: any;
  subscription: Subscription;
  loadingData: boolean = false;
  loadingCount: number = 1;
  
  setOrder(value: string) {
    this.order = value;
    if (this.order === value) {
      this.userSearch.sortOrder = this.reverse ? "DESC" : "ASC";
      this.userSearch.sort = value;
      this.reverse = !this.reverse;
      this.getUsers();
    }
  }
  
  constructor(
    private toastr: ToastrService,
    private spinner: NgxSpinnerService,
    private globalService: GlobalService,
    private jwtService: JwtService,
    private route: ActivatedRoute,
  ) {
    this.currentUser = JSON.parse(this.jwtService.getCurrentUser());
    this.globalService.setLoadingLabel('Loading... Please Wait.');
    this.globalService.sendActionChildToParent('users');
    this.companySearch.user = this.currentUser.id;
    this.route.params.subscribe((res) => {
      if(res.companyId){
        this.companyId = res.companyId; 
      }
    });
    if(this.currentUser['isSuccessHackerAdmin']){
      this.permission = {
        view: true,
        edit: true,
        delete: true
      }
    }
  }

  ngOnInit() {
    this.getCompanies();
  }

  getCompanies(){
    if(!this.currentUser['isSuccessHackerAdmin']){
      this.userSearch.company = this.currentUser['company'];
      this.getUsers();
      this.getGroups(this.userSearch.company);
    }else{
      this.companies = [];
      this.spinner.show();
      this.loadingData = true;
      this.globalService.getCompanies({user: this.companySearch.user}).subscribe(data => {
        this.spinner.hide();
        if(Object.keys(data).length){
          this.companies = data.items;
          let found = this.companies.filter(e => e.companyId == this.currentUser['company'].companyId);
          if(this.companyId){
            let foundc = this.companies.filter(e => e.companyId == this.companyId);
            if(foundc.length){
              this.userSearch.company = foundc[0];
              this.getGroups(this.userSearch.company);
              this.getUsers();
            }
          }else{
            if(this.currentUser['company']){
              this.userSearch.company = found[0];
              this.getGroups(this.userSearch.company);
              this.getUsers();
            }
          }
        }
      }, error => {
        this.loadingData = false;
        this.spinner.hide();
        if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
      });
    }
  }

  getGroups(company: any){
    if(company && company.groups && company.groups.length){
      this.groups = company.groups;
    }
  }

  getUsers(){
    this.pagination = {};
    if(this.userSearch.company){
      let postObject = {
        page: this.userSearch.page,
        limit: this.userSearch.limit,
        company: this.userSearch.company.companyId,
        user: this.currentUser.id,
        group: this.userSearch.group,
        search: this.userSearch.search,
        sort: this.userSearch.sort,
        sortOrder: this.userSearch.sortOrder,
      }
      if(this.userSearch.isActive){
        postObject['isActive'] = Number(this.userSearch.isActive);
      }
      this.globalService.setLoadingLabel('Loading... Please Wait.');
      this.spinner.show();
      if(this.loadingCount < 2){
        this.loadingData = true;
      }
      this.globalService.getUsers(postObject).subscribe(data => {
        this.loadingData = false;
        this.loadingCount++;
        this.spinner.hide();
        if(data && Object.keys(data).length){
          this.users = data.items;
          this.pagination = data.meta.pagination;
        }else{
          this.users = [];
        }
      }, error => {
        this.spinner.hide();
        this.loadingData = false;
        if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
      });
    }else{
      alert("Please select the company first!");
    }
  }

  showDeleteModal(type: string, user: any){
    this.userDeleteOption = type;
    this.selectedUser = user;
    this.deleteUserModal.show();
  }

  multiSelectCompany(multiSelect: any){
    this.selectedUsers = [];
    this.users.forEach(element => {
      if(multiSelect){
        element.added = false;
      }else{
        element.added = true;
        this.selectedUsers.push(element)
      }
    });
  }

  selectUser(user: object){
    let found = this.selectedUsers.filter(e => e.userId == user['userId']);
    if(found.length){
      this.selectedUsers.splice(this.selectedUsers.indexOf(found[0]), 1);
    }else{
      this.selectedUsers.push(user);
    }
  }

  deleteUser(){
    let postData = {
      firstName: this.selectedUser.firstName,
      lastName: this.selectedUser.lastName,
      emailAddress: this.selectedUser.emailAddress,
      isActive: 0,
      customerAdmin: this.selectedUser.isCustomerAdmin,
      successHackerAdmin: this.selectedUser.isSuccessHackerAdmin,
      company: this.selectedUser.company.companyId,
      groups: this.selectedUser.groups,
      modifier: this.currentUser.id,
    }
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.globalService.updateUser(postData, this.selectedUser['userId']).subscribe(data => {
      this.spinner.hide();
      if(data && data.modifiedBy){
        this.toastr.success( "Data updated successfully.", "Success");
        this.selectedUser.isActive = data.isActive;
      }else{
        this.toastr.error("There are some error while updating the data!", "Error");
      }
      this.deleteUserModal.hide();
    }, error => {
      this.spinner.hide();
      this.deleteUserModal.hide();
      if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
    });
  }

  deleteAllUser(){
    this.globalService.setLoadingLabel('Updating... Please Wait.');
    this.spinner.show();
    this.selectedUsers.forEach((element: any, index: Number) => {
      let postData = {
        firstName: element.firstName,
        lastName: element.lastName,
        emailAddress: element.emailAddress,
        isActive: 0,
        customerAdmin: element.isCustomerAdmin,
        successHackerAdmin: element.isSuccessHackerAdmin,
        company: element.company.companyId,
        groups: element.groups,
        modifier: this.currentUser.id,
      }
      this.globalService.updateUser(postData, element['userId']).subscribe(data => {
        if(index == this.selectedUsers.length-1){
          this.spinner.hide();
          this.deleteUserModal.hide();
          this.getUsers();
          this.toastr.success( "Data updated successfully.", "Success");
        }
      }, error => {
        this.spinner.hide();
        this.deleteUserModal.hide();
        if(error &&  error.error && error.error.message){
          this.toastr.error(error.error.message, 'Error '+error.status );
        }else{
          this.toastr.error( environment.reponseCode[error.status] , 'Error' );
        }
      });
    });
  }

  uploadFile($event){
    let file = $event.target.files[0];
    let postData = {
      company: this.userSearch.company.companyId,
      user: this.currentUser.id
    }
    this.spinner.show();
    this.globalService.uploadFile(file, postData).subscribe( (data: any) => {
      this.spinner.hide();
      this.toastr.success('Data imported successfully.', 'Success' );
      this.getUsers();
    }, error => {
      this.spinner.hide();
      if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
    });
  }

  exportData(){
    this.globalService.setLoadingLabel('Exporting... Please Wait.');
    this.spinner.show();
    let postObject = {
      company: this.userSearch.company.companyId,
      group: this.userSearch.group,
      search: this.userSearch.search,
      user: this.currentUser.id
    }
    if(this.userSearch.isActive){
      postObject['isActive'] = Number(this.userSearch.isActive);
    }
    this.globalService.exportData(postObject).subscribe(data => {
      this.spinner.hide();
      if(data){
        const blobData = new Blob([data['output']], { type: 'text/csv' });
        const url = window.URL.createObjectURL(blobData);
        saveAs(blobData, "users.csv");
      }
    }, error => {
      this.spinner.hide();
      if(error &&  error.error && error.error.message){
        this.toastr.error(error.error.message, 'Error '+error.status );
      }else{
        this.toastr.error( environment.reponseCode[error.status] , 'Error' );
      }
    });
  }

  callUserFunction(){
    let time = 3000;
    if(this.userSearch.search != ""){
      setTimeout (() => {
      }, time);
    }
  }
  
  timeout: any = null;
  onKeySearch(event: any) {
    clearTimeout(this.timeout);
    var $this = this;
    this.timeout = setTimeout(function () {
      if (event.keyCode != 13) {
        $this.getUsers();
      }
    }, 1000);
  }

  clearFilter(){
    this.userSearch.search = "";
    this.userSearch.isActive = "";
    this.userSearch.group = "";
    this.userSearch.sort = "lastName";
    this.userSearch.sortOrder = 'ASC';
    this.reverse = true;
    this.getUsers();
  }

}
